import { decryptString, encrypt } from '../utils/crypto-aes.js';

export const CryptoStrTr = {
    to: (text: string | null): string | null => {
        if (!text) {
            return text;
        }
        return encrypt(text);
    },
    from: (internalCiphertext: string | null): string | null => {
        if (!internalCiphertext) {
            return internalCiphertext;
        }
        return decryptString(internalCiphertext);
    },
};
